<template>
  <div class="all">

    <div class="value">

    </div>

    <div class="translation">
      <el-input
        type="textarea"
        :rows="2"
        placeholder="请输入单词"
        v-model="textarea"
        size="medium"
        id="value"
        ref="value">
      </el-input>

    </div>

    <!-- <p v-if="this.success">{{this.word}}  录入成功</p> -->
    <div v-if="this.addWordList.length">
      <h5>录入的单词</h5>
    <ul id="addWordList" v-for="(w,index) in this.addWordList" :key="index">
        <li>{{w}}</li>

    </ul>
    </div>

    <el-button type="primary"
      plain id="btn3"
      @click="addWord"
      
      >录入
    </el-button>

  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'AddWords',
  data() {
    return {
      textarea: '',
      success: false,
      word: '',
      addWordList: [],
    }
  },

  methods: {
    onClickEnter() {
      this.addWord()
    },
    open4(msg) {
        this.$notify.error({
          title: 'warning',
          message: msg,
          type: 'warning'
        });
      },
    addWord() {
      let regx = /^[A-Za-z\x20]+$/

      if (this.textarea == '' || this.textarea == null) {
        this.open4("请输入内容")
        this.textarea = ''
      } else {
        if (!regx.test(this.textarea)) {
         
          this.open4("请输入单词")
          this.textarea = ''
        } else {
          axios({
            method: 'post',
            url: this.$baseURL+'/word/addWord',
            data: {
              uid: this.$store.state.uid,
              value: this.textarea,
            },
          }).then((res) => {
            // console.log(res)
            if (res.data.status == 200) {
              this.success = true
              this.word = this.textarea
              //给数组开头添加单词进去,达到倒序显示的效果
              this.addWordList.unshift(this.word)
              this.open1(this.word)
              this.textarea = ''
            } else {
              this.success = false
              this.textarea = ''
              // alert(res.data.message)

              this.open4(res.data.message)
            }
          })
        }
      }
    },
    open1(word) {
      this.$notify({
        title: '添加成功',
        message: '单词' + word + '添加成功',
        type: 'success',
        position: 'bottom-right',
      })
    },
  },
  mounted() {
    this.$keyBoard(this, 'onClickEnter', 13) //13是enter按键  其他按键码自己查
    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid === undefined
    ) {
      alert('请先登录')
      this.$router.push('/pc/login')
    }
  },
}
</script>
<style scoped>
#btn3 {
  position: absolute;
  width: 12.5rem;
  top: 12.5rem;
  right: 6rem;
  height: 3.75rem;
  font-size: 1.5rem;
}

#el-input {
  font-size: 12.5rem;
}
ul li {
  list-style: none;
}

#addWordList{
  margin-left: 3rem;
  left: .3125rem;
}
</style>
